.navbar {
  align-items: baseline !important;
}
.labelDiv {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.labelDiv h6 {
  font-size: 14px;
}
.activeContainerButton {
  color: #fff;
  background-color: #28a745;
  border-color: #28a745;
  box-shadow: none;
}
.containerTableTr:hover {
  cursor: pointer;
  color: #fff !important;
  background-color: #007bff !important;
  border-color: #007bff !important;
}
.customBreadCrumb {
  background: none;
  padding: 0;
  margin-left: 0;
}
#addModalButton,
#moveButton {
  width: 100px;
}
.breadcrumb-item + .breadcrumb-item {
  padding-left: 0.25rem;
}
.breadcrumb-item + .breadcrumb-item::before {
  padding-right: 0.25rem;
  margin-top: -2px !important;
}
.customBreadCrumb a {
  font-size: 14px;
}
.breadcrumb-item.active a {
  color: #6c757d !important;
  pointer-events: none;
  text-decoration: none;
}
.drillDownFont {
  font-size: 12px;
  margin-bottom: 0;
}
.containersTable td,
.containersTable th {
  text-align: center;
}
